import { useContext, useState } from "react";
import PropTypes from "prop-types"
function Button(props) {
  let { type, children, onClick } = props;
  return (
    <div className="ml-button">
            {/* onClick 叫React的合成事件 */}
            <span className={type} onClick={onClick}>{children}</span>
        </div>
  )
}
Button.propTypes = {
    type: PropTypes.oneOf(["default", "primary", "danger", "info"]),
    children: PropTypes.node,
    onClick: PropTypes.func,
}
Button.defaultProps = {
    type: 'default',
    children: '按钮',
    onClick: () => { },
}

//头部组件
function ModalHeader(props) {
  const {title,closable,onCancel} = props
  return (
    <div>
      <div>{title}</div>
      <div onClick={onCancel}>{closable && "❌"}</div>
   </div>
  )
}
//主体
function ModalMain(props) {
  // console.log(props.children.value);
  const { inputValue,onChange } = props
  const changeInput = (e) => {
    onChange(e.target.value)
  }
  return (
        <div>
          <label htmlFor="user" >添加用户:</label>
      <input type="text" id="user" value={inputValue} onChange={changeInput } />
        </div>
  )
}
//底部
function ModalFooter(props) {
  const { type, onCancel, onOk, footer } = props
  const renderFooter = () => {
    let btn = [];
    switch (type) {
      case 'confirm':
        btn = [
          <Button type="primary" key="1" onClick={onOk}>确定</Button>,
          // Button是组件, onClick是事件函数  是一个特殊的props
          <Button key="2" onClick={onCancel}>取消</Button>
        ]
        break;
      case 'danger':
        btn = [
          <Button type="primary" key="1" onClick={onOk}>删除</Button>,
          // Button是组件, onClick是事件函数  是一个特殊的props
          <Button key="2" onClick={onCancel}>取消</Button>
        ]
        break;
      case 'info':
        btn = [
          <Button type="info" key="1" onClick={onCancel}>我知道了</Button>
        ]
        break;
    }
    return btn
  }
   return (
    footer ? footer() : renderFooter()
  )
}
function Modal(props) {
  //显示隐藏
  const {visible} =props
  return (
    <div className="ml-layer" style={{display:visible?'block':'none'}}>
      <div className="ml-modal">
        <header>
          <ModalHeader {...props} ></ModalHeader>
        </header>
        <main>
          <ModalMain {...props} ></ModalMain>
        </main>
        <footer>
          <ModalFooter {...props} ></ModalFooter>
        </footer>
      </div>
    </div>
  )
}
//设置Modal就需要校验和设置默认值
Modal.propTypes = {
  title: PropTypes.elementType,
  inputValue: PropTypes.elementType,
  type: PropTypes.oneOf(["confirm", "danger", "info"]),
  visible: PropTypes.bool,
  onCancel: PropTypes.func,
  onChange: PropTypes.func,
  closable: PropTypes.bool,//是否显示右上角的关闭按钮
  children: PropTypes.node,
  onOk: PropTypes.func,
  footer: PropTypes.func,
  // content:PropTypes.array
}
//默认值
Modal.defaultProps = {
  title: '默认标题',
  visible: false,
  type: 'info',
  closable: true,
  inputValue:'请输入用户名',
  children: <div>主体内容默认值</div>,
  onCancel: () => { },
  onChange: () => { },
  onOk: () => { },
  footer: () => { },
  // content:[]
}
function PageA() {
  //定义弹窗 是否显示
  const [visible, setVisible] = useState(false)
  //定义一个input内容
  const [userVal, setUserVal] = useState('')
  const [content,setContent] =useState([])
  //定义一个确定点击事件
  const submit=()=>{
    setTimeout(() => {
            console.log("submit...");
            console.log("发送ajax请求...");

      setVisible(false)
      setUserVal('')
        }, 200)
  }
  const changeInput = (val) => {
    setUserVal(val)
  }
  const determine = () => {
    setContent([...content,userVal])
    setVisible(false)
    setUserVal('')
  }
  return (
    <div>
      <button onClick={() => setVisible(true)} >点我弹出组件</button>
      <div>
         {
        content.map((item,index) => {
          return (
              <p key={index} >用户名：{item}</p>
          )
        })
      }
     </div>
      <Modal
        title={'添加用户'}
        closable
        visible={visible}
        // type="confirm"
        inputValue={userVal}
        content={content}
        onCancel={() => setVisible(false)}
        onChange={changeInput }
        onOk={submit}
        footer={
          () => {
            return [
              <Button type='danger' key="1" onClick={() => setVisible(false)}>残忍离开</Button>,
              <Button type='primary' key="2" onClick={determine}>确定</Button>
            ]
          }
        }
      >
      </Modal>
    </div>
  )
}
export default PageA